import { useContext } from "react";
import LanguageContext from "../../contexts/LanguageContext";

import { Select, MenuItem } from "@mui/material";

import prevod from "./prevod";

import { getLanguage } from "../../utils/language";

const LanguageSwitcherBurger = () => {
  const { language, setLanguage } = useContext(LanguageContext);

  const languageData = getLanguage(prevod, language);

  return (
    <>
      <Select
        value={language}
        onChange={(e) => setLanguage(e.target.value)}
        style={{ margin: 10, color: "#41d2bf" }}
      >
        <MenuItem value="en">{languageData.en}</MenuItem>
        <MenuItem value="lat">{languageData.lat}</MenuItem>
        <MenuItem value="cir">{languageData.cir}</MenuItem>
      </Select>
    </>
  );
};

export default LanguageSwitcherBurger;
